import React, { useState } from "react";
import logos from "../../assets/pictures/logos.png";
import {
  FooterContainer,
  Contact,
  Logos,
  CardFooterContainer,
} from "./footerStyle";
import SecBanner from "./banner/SecBanner";

export default function Footer({ card }) {
  const [textPop, setTextPop] = useState(false);
  const [showPopUp, setShowPopUp] = useState(false);
  const containerStyle = {
    display: "flex",
    flexDirection: "row-reverse",
    textAlign: "center",
    color: "white",
    marginTop: "0",
    fontWeight: "bold",
    fontSize: "0.7rem",
    padding: "2rem 0.5rem",
  };
  const unselectedStyle = {
    margin: "auto",
    borderRadius: "9px",
    marginLeft: "10px",
    marginRight: "10px",
    width: "4.8rem",
    padding: "2px 0px",
    justifyContent: "space-around",
    display: "flex",
    alignItems: "center",
    background: "none",
    border: "1px solid #657c54",
  };
  const labels = [
    {
      name: " ",
      icon: require("../../assets/pictures/email.png"),
      title: ` `,
      text: `    
      artechb15@gmail.com
      `,
      style: { height: "25%", position: "absolute", marginTop: "17rem"},
    },
    {
      name: "",
      icon: require("../../assets/pictures/odot.svg"),
      title: `"   `,
      text: `
              - ,      
           .        ,
                    
               .      '50
              -50' -   -  
           '',     "  " " 
          , " '  .      
               ,   
           .          
            -           
          .  !`,
    },
  ];

  return (
    <>
      {card ? (
        ""
      ) : (
        <FooterContainer>
          <Logos src={logos} />
            "  ,   "
          <div style={containerStyle}>
            {labels.map((label) => (
              <div
                key={label}
                style={unselectedStyle}
                onClick={() => {
                  setShowPopUp(true);
                  setTextPop(label);
                }}
              >
                <img
                  src={label.icon}
                  style={{ width: "20px", height: "20px" }}
                ></img>
                {label.name}
              </div>
            ))}
          </div>
        </FooterContainer>
      )}
      {showPopUp && <SecBanner setShowPopUp={setShowPopUp} label={textPop} />}
    </>
  );
}
